.people-wrapper {
  width: 100%;
  height: auto;
  display: flex;
  position: relative;
  background: #f3f6f7;
}

.people-wrapper-after {
  width: 20%;
  height: 10%;
  background: rgba(0, 0, 0, 0.8);
  position: absolute;
  bottom: 20px;
  left: 20px;
  z-index: 3;
  border-radius: 5px;
  color: rgb(255, 255, 255);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: var(--font13);
  min-width: 275px;
  min-height: 50px;
  max-height: 50px;
  opacity: 0.95;
  transition: 0.1s ease-out;
  font-weight: 300;
  user-select: none; /* supported by Chrome and Opera */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
}

.people-content {
  width: 95%;
  height: 100vh;
  background: #f3f6f7;
  overflow-y: scroll;
  padding-bottom: 20px;
}

#input-checkbox {
  margin-right: 10px;
}

.people-content-body {
  width: 95%;
  height: auto;
  margin: 0 auto;
  border-radius: 10px;
  position: relative;
}

.people-content-body-wrapper {
  height: auto;
  width: 100%;
  border-radius: 10px;
}

.people-people-teacher-body {
  width: 100%;
  height: auto;
  background: rgb(255, 255, 255);
  border: 1px var(--border);
  border-radius: 10px;
  margin-bottom: 20px;
}

.people-people-student-body {
  width: 100%;
  height: auto;
  background: rgb(255, 255, 255);
  border: 1px var(--border);
  border-radius: 10px;
}

.people-teacher-header {
  width: 100%;
  height: 10vh;
  max-height: 60px;
  min-height: 50px;
  display: flex;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  justify-content: space-between;
  border-bottom: 1px solid rgb(228, 228, 228);
  color: #323b44;
}

.people-teacher-header span {
  background: rgb(224, 227, 228);
  padding: 10px 15px 10px 10px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 15px;
  transition: 0.2s ease-out;
}

.people-teacher-header span:hover {
  background: rgb(205, 209, 211);
  transition: 0.2s ease-in;
}

.people-teacher-body {
  width: 100%;
  height: 10vh;
  display: flex;
  align-items: center;
  padding-left: 20px;
  color: var(--font);
  margin-bottom: 10px;
  font-size: 15px;
}

.people-teacher-body p {
  margin-right: 5px;
}

.people-student-body {
  width: 100%;
  height: auto;
  min-height: 200px;
  align-items: center;
  padding-left: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
  color: #707983;
  font-size: 15px;
}

.people-student-body-wrapper {
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  color: var(--font);
}

/* Add people */

.add-people {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  background: rgb(255, 255, 255);
  border-radius: 10px;
  border: 1px var(--border);
  padding-bottom: 15px;
  margin-bottom: 20px;
}

.add-people-header {
  width: 100%;
  height: 10vh;
  max-height: 60px;
  min-height: 50px;
  display: flex;
  align-items: center;
  padding-right: 20px;
  justify-content: flex-end;
  font-size: 20px;
  color: var(--font);
}

.add-people-header i {
  padding: 10px 13px;
  border-radius: 50%;
  cursor: pointer;
  transition: 0.2s ease-out;
}

.add-people-header i:hover {
  background: rgb(224, 227, 228);
  transition: 0.2s ease-in;
}

.addpeople-wrapper {
  width: 100%;
  height: 100%;
}

.people-wrapper-add-student-body {
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  padding-left: 20px;
  font-size: var(--font14);
  color: var(--sub-font);
}

.people-wrapper-add-student-body-0 {
  width: 100%;
  height: 30px;
  display: flex;
  align-items: center;
  padding-left: 20px;
  font-size: var(--font15);
  color: var(--font);
  font-weight: 500;
}

.add-people-btn {
  background: var(--main);
  width: 60px;
}

.add-people-btn-opacity {
  background: red;
}
